nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: flex;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    border: 1px solid;
    border-color: var(--color-primary);
}

nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(211, 16, 16, 0.3);
}

nav a.active {
    background: radial-gradient(circle at 50% -20.71%, var(--color-primary), var(--color-primary-variant));
    color: var(--color-white)
}

 /* ========= Media Queries (Small Devices) ============== */
@media screen and (max-width: 600px) {
    nav, nav a {
        padding: 0.7rem;
    }
} 